import { Component, OnInit } from '@angular/core';
import * as FHIR from 'fhirclient';
import { Subject } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { PatientDetails, Resource } from '../Model/patientdata';
import { ActivatedRoute, Params } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-patientdata',
  templateUrl: './patientdata.component.html',
  styleUrls: ['./patientdata.component.scss'],
})
export class PatientdataComponent implements OnInit {
  FHIRClients: Resource;
  patientid: string;
  asyncCall;
  constructor(private route: ActivatedRoute, private SpinnerService: NgxSpinnerService) {}

  ngOnInit(): void {
    // this.SpinnerService.show();
    this.route.params.subscribe((params: Params) => {
      this.patientid = params.id;
    });
    this.AsyncallFUnction();

    // (window as any).FHIR = FHIR;
    // FHIR.oauth2
    //   .ready()
    //   .then((client) => client.request(`Patient/${this.patientid}`))
    //   .then((result) => this.mapData(result))
    //   .catch(console.error);
  }

  // mapData(result) {
  //   this.FHIRClients = result;
  //   // console.log(result);
  // }

  async AsyncallFUnction() {
    try {
     
      (window as any).FHIR = FHIR;
      this.asyncCall = await (await FHIR.oauth2.ready()).request(
        `Patient/${this.patientid}`
      );
      if (this.asyncCall) {
        this.FHIRClients = this.asyncCall;
        // this.SpinnerService.hide();
        // console.log(this.asyncCall);
      }
    } catch {
      console.log(Error);
    }
  }
}
